import React, { Component } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";

import { withRouter } from "react-router-dom";

import { withStyles } from "@material-ui/core/styles";
import Drawer from "@material-ui/core/Drawer";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import List from "@material-ui/core/List";
import Typography from "@material-ui/core/Typography";
import Divider from "@material-ui/core/Divider";
import IconButton from "@material-ui/core/IconButton";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import Tour from "reactour";
import { blockly, assessment } from "./Tour";
import {
  faBars,
  faChevronLeft,
  faLayerGroup,
  faQuestionCircle,
  faHome
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import * as Blockly from "blockly";
import Tooltip from "@material-ui/core/Tooltip";

const styles = (theme) => ({
  drawerWidth: {
    width: window.innerWidth < 600 ? "100%" : "240px",
    borderRight: `1px solid ${theme.palette.primary.main}`
  },
  appBarColor: {
    backgroundColor: theme.palette.primary.main
  },
  tourButton: {
    marginleft: "auto",
    marginright: "30px"
  }
});

class Navbar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      open: false,
      isTourOpen: false
    };
  }

  toggleDrawer = () => {
    this.setState({ open: !this.state.open });
  };

  openTour = () => {
    this.setState({ isTourOpen: true });
  };

  closeTour = () => {
    this.setState({ isTourOpen: false });
  };

  render() {
    var isBlockly = /^\/blockly(\/.*$|$)/g.test(this.props.location.pathname);
    return (
      <div>
        <AppBar
          position="fixed"
          style={{
            height: "50px",
            marginBottom: "30px",
            boxShadow:
              "0px 2px 4px -1px rgba(0,0,0,0.2),0px 4px 5px 0px rgba(0,0,0,0.14),0px 1px 10px 0px rgba(0,0,0,0.12)",
            top: "0px"
          }}
          classes={{ root: this.props.classes.appBarColor }}
        >
          <Toolbar
            style={{
              height: "50px",
              minHeight: "50px",
              padding: 0,
              color: "white"
            }}
          >
            <IconButton
              color="inherit"
              onClick={this.toggleDrawer}
              style={{ margin: "0 10px" }}
              className="MenuButton"
            >
              <FontAwesomeIcon icon={faBars} />
            </IconButton>
            <a
              href={"https://www.bangaramrobotics.com/"}
              style={{ textDecoration: "none", color: "inherit" }}
            >
              <Typography variant="h6" noWrap>
                Bangaram Robotics
              </Typography>
            </a>
            {isBlockly ? (
              <Tooltip title="Start Tour" arrow>
                <IconButton
                  color="inherit"
                  className={`openTour ${this.props.classes.button}`}
                  onClick={() => {
                    this.openTour();
                  }}
                  style={{ margin: "0 30px 0 auto" }}
                >
                  <FontAwesomeIcon icon={faQuestionCircle} />
                </IconButton>
              </Tooltip>
            ) : null}
            <Tour
              steps={isBlockly ? blockly() : assessment()}
              isOpen={this.state.isTourOpen}
              onRequestClose={() => {
                this.closeTour();
              }}
            />
          </Toolbar>
        </AppBar>
        <Drawer
          variant="temporary"
          anchor="left"
          onClose={this.toggleDrawer}
          open={this.state.open}
          classes={{ paper: this.props.classes.drawerWidth }}
          ModalProps={{ keepMounted: true }} // Better open performance on mobile.
        >
          <div
            style={{
              height: "50px",
              cursor: "pointer",
              color: "white",
              padding: "0 22px"
            }}
            className={this.props.classes.appBarColor}
            onClick={this.toggleDrawer}
          >
            <div
              style={{
                display: " table-cell",
                verticalAlign: "middle",
                height: "inherit",
                width: "0.1%"
              }}
            >
              <Typography variant="h6" style={{ display: "inline" }}>
                {Blockly.Msg.navbar_menu}
              </Typography>
              <div style={{ float: "right" }}>
                <FontAwesomeIcon icon={faChevronLeft} />
              </div>
            </div>
          </div>
          <List>
            {[{ text: "Home", icon: faHome, link: "https://www.bangaramrobotics.com/" }].map(
              (item, index) => {
                if (
                  item.restriction ||
                  Object.keys(item).filter(
                    (attribute) => attribute === "restriction"
                  ).length === 0
                ) {
                  return (
                    <a
                      href={item.link}
                      key={index}
                      style={{ textDecoration: "none", color: "inherit" }}
                    >
                      <ListItem button onClick={this.toggleDrawer}>
                        <ListItemIcon>
                          <FontAwesomeIcon icon={item.icon} />
                        </ListItemIcon>
                        <ListItemText primary={item.text} />
                      </ListItem>
                    </a>
                  );
                } else {
                  return null;
                }
              }
            )}
            {[{ text: "Blockly", icon: faLayerGroup, link: "/bangaram-ide" }].map(
              (item, index) => {
                if (
                  item.restriction ||
                  Object.keys(item).filter(
                    (attribute) => attribute === "restriction"
                  ).length === 0
                ) {
                  return (
                    <Link
                      to={item.link}
                      key={index}
                      style={{ textDecoration: "none", color: "inherit" }}
                    >
                      <ListItem button onClick={this.toggleDrawer}>
                        <ListItemIcon>
                          <FontAwesomeIcon icon={item.icon} />
                        </ListItemIcon>
                        <ListItemText primary={item.text} />
                      </ListItem>
                    </Link>
                  );
                } else {
                  return null;
                }
              }
            )}
          </List>
        </Drawer>
      </div>
    );
  }
}

Navbar.propTypes = {};

const mapStateToProps = (state) => ({});

export default connect(mapStateToProps)(
  withStyles(styles, { withTheme: true })(withRouter(Navbar))
);
